
  .card {
    box-shadow: 0 4px 8px 0 rgba(241, 239, 239, 0.2);
    transition: 0.3s;
    border-radius: 5px; /* 5px rounded corners */
    background-color: #b0b6b6;
    width: 300px;
    height: fit-content;
    align-self: center;
    box-sizing: border-box;

  }
  .container {
    padding: 2px 16px;
    background-color: #2c2d2e;
    width: fit-content;
    margin-left: 500px;
    border-radius: 10px;
  }
  
.card:hover {
  box-shadow: 0 8px 16px 0 rgba(248, 245, 245, 0.397);
}
.display{
  background-color: #fffefe;
  height: 100px;
  margin-bottom: 20px;
  width: 80%;
  margin-left: 100px;
  border-radius: 10px;

}
.wrapper{
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}
.wrapper > * {
  padding: 10px;
}
.input{
  width: fit-content;
  align-self: center;
  margin-top: 10px;
}
.button{
  background-color: #9b9794;
  width: fit-content;
  align-self: center;

}
